/* ANIMAL DETAIL CARDS *//



.box {
  width: 800px;  
  margin: auto;
  position: relative;
  
}

#card-container {
  position: relative;
  margin: 3.5rem;
  width: 20rem;
  height: 20rem;
  z-index: 1;
  float: left;
  perspective: 1000px;
}

#card {
  position: relative;
  width: 100%;
  height: 100%;
  margin: auto;
  transform-style: preserve-3d;
  transition: all .8s linear;
  box-shadow: 5px 5px 15px rgba(0,0,0, .3);
}

#card:hover {
    transform: rotateY(180deg);
    box-shadow: -5px 5px 15px rgba(0,0,0, .3);
  }


.face {
  text-align: center;
  padding-top: 6rem;
  padding-bottom: auto;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  background-color: #d08a55
  
}

.face h1 {
  font-size: 3rem;
  color: #272727;
}
.face h2 {
  font-size: 1rem;
  margin-top: -2rem;
  color: #272727;
}

.back {
  display: block;
  transform: rotateY(180deg);
  box-sizing: border-box;
  padding: 10px;
  text-align: center;
  opacity: 100;
  background: #ebb28e;
}



  
 /*BUTTON TO ANIMAL DETAIL */ 
.btn-2 {
  letter-spacing: 0;
}

.btn-2:hover,
.btn-2:active {
letter-spacing: 5px;
}

.btn-2:after,
.btn-2:before {
backface-visibility: hidden;
border: 1px solid #272727;
bottom: 0px;
content: " ";
display: block;
margin: 0 auto;
position: relative;
transition: all 280ms ease-in-out;
width: 0;
}

.btn-2:hover:after,
.btn-2:hover:before {
backface-visibility: hidden;
border-color: #272727;
transition: width 350ms ease-in-out;
width: 70%;
}

.btn-2:hover:before {
bottom: auto;
top: 0;
width: 70%;
}    

