/* TINDER APP  */
.heart-wrapper {
  width: 70px;
  height: 70px;
  position: relative;
  margin:1rem auto;
  background-color: white;
  border-radius: 40px;
  
  -webkit-box-shadow: 0px 3px 29px 0px rgba(160, 160, 160, 1);
  -moz-box-shadow: 0px 3px 29px 0px rgba(160, 160, 160, 1);
  box-shadow: 0px 3px 29px 0px rgba(160, 160, 160, 1);
  transition: 0.3s;
}
.heart-wrapper-2 {
  width: 70px;
  height: 70px;
  position: relative;
  margin: auto;
  background-color: white;
  border-radius: 40px;
  
  -webkit-box-shadow: 0px 3px 29px 0px rgba(160, 160, 160, 1);
  -moz-box-shadow: 0px 3px 29px 0px rgba(160, 160, 160, 1);
  box-shadow: 0px 3px 29px 0px rgba(160, 160, 160, 1);
  transition: 0.3s;
}
.heart-wrapper:hover {
  -webkit-box-shadow: 0px 2px 15px 0px rgba(117, 117, 117, 1);
  -moz-box-shadow: 0px 2px 15px 0px rgba(117, 117, 117, 1);
  box-shadow: 0px 2px 15px 0px rgba(117, 117, 117, 1);
}
.heart-wrapper-2:hover {
  -webkit-box-shadow: 0px 2px 15px 0px rgba(117, 117, 117, 1);
  -moz-box-shadow: 0px 2px 15px 0px rgba(117, 117, 117, 1);
  box-shadow: 0px 2px 15px 0px rgba(117, 117, 117, 1);
}
.fa-heart {
  position: absolute;
  margin: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  color: #ff8484;
  transition: 1s;
}
.icon-list {
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;

}
.fa-times {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 40px;
  color: #ff8484;
  transition: 1s;
}
.background-heart{
  background-color:white;
}
.background-heart::after {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f004";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 38px;
  color: red;
  transition: 1s;
  z-index: -1;
  animation: fill-heart 1s;
}

.background-heart::before {
  content: "";
  width: 60px;
  height: 60px;
  opacity: 0.2;
  background-color: red;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1;
  border-radius: 200px;
  animation: circle-expand 1s;
}

@keyframes fill-heart {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes circle-expand {
  0% {
    width: 60px;
    height: 60px;
    opacity: 1;
  }
  40% {
    width: 0px;
    height: 0px;
    opacity: 0.2;
  }
  80% {
    width: 60px;
    height: 60px;
    opacity: 0.2;
  }

  100% {
    width: 60px;
    height: 60px;
    opacity: 0.2;
  }
}
